/* , 'Roboto' 'Roboto Condensed', 'Lato', 'Montserrat',  */
*{
  font-family: 'Inter','Overpass', 'Noto Serif JP', 'Roboto Condensed', kozuka-gothic-pr6n, neue-haas-grotesk-text, dnp-shuei-gothic-kin-std, europa, brix-slab,  sans-serif;
  font-weight: 400;
  
}

h1{
  font-family: adobe-handwriting-frank;
}

h2, h3, h4, h5, h6, dt, span.a, .nav-link, .header, .counter{
  font-family:  'Inter', 'Overpass', 'Noto Serif JP', 'Montserrat', 'Bitter', 'Karla', 'Noto Serif JP', kozuka-gothic-pr6n, neue-haas-grotesk-display, brix-slab, fot-rodin-pron;
}


p, dd, li{
  color: #808080;
  text-align: justify;
}

@media only screen and (max-width: 576px) {
  p, dd, li, span{
    font-size: 0.9rem;
    text-align: start;
  }

}

.carousel-control-next, .carousel-control-prev{
  bottom: 40px;
}
.carousel-indicators{
  bottom: -40px;
}

.carousel-indicators li{
  width: 10px;
  height: 10px;
  background-color: #808080;
  opacity: 0.1;
}